.App{
  height : 32rem;
  width: 20rem;
  background-image: radial-gradient(#adb1b5, #4d4f50);
  margin: 4rem auto;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  border-radius: 12px;
}
.wheel {
  width : 74%;
  height : 40%;
  margin : 0.25rem auto;
  background-color:#4b4e52 ;
  border-radius: 50%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.buttonContainer {
  width : 85%;
  height : 30%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.menuButton {
  align-self: center;
}

.playButton {
  align-self: center;
}

.middleButtons {
  align-self: center;
  width : 100%;
  display : flex;
  flex-direction: row;
  justify-content: space-between;
}
.image{
  align-self: center;
  font-Size: 1.5rem;
  color : white;
}

